/*upload file*/


.input-file .button input {  
  position: absolute;  
  top: 0;  
  right: 0;  
  padding: 0;  
  font-size: 30px;  
  cursor: pointer;  
  opacity: 0;  
}  
.input input {  
    display: block;  
    box-sizing: border-box;  
    -moz-box-sizing: border-box;  
    width: 100%;  
    height: 28px;  
    padding: 8px 10px;  
    outline: 0;  
    border-width: 1px;  
    border-style: solid;  
    border-radius: 0;  
    background: #fff;  
    font: 13px/16px 'Open Sans', Helvetica,Arial, sans-serif;  
    color: #404040;  
    appearance: normal;  
    -moz-appearance: none;  
    -webkit-appearance: none;  
}   



